import { Button, Col, Form, Row } from "@themesberg/react-bootstrap";
import React, { useState } from "react";
import { useHistory, useLocation } from "react-router-dom";
import axiosInstance from "../axiosConfig";
import GlobalToast from "../components/GlobalNotification/GlobalNotification";

const ResetPassword = () => {
  const params = useLocation();
  const [newPassword, setNewPassword] = useState("");
  const [confirmedNewPassword, setConfirmedNewPassword] = useState("");
  const [error, setError] = useState({
    status: false,
    message: "",
  });
  const history = useHistory();
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await axiosInstance.post("/auth/reset-password", {
        code: params.search.split("=")[1], // code contained in the reset link of step 3.
        password: newPassword,
        passwordConfirmation: confirmedNewPassword,
      });
      history.push("/login");
    } catch (error) {
      const err = {
        status: true,
        message: error.response.data.message[0].messages[0].message,
      };
      setError(err);
    }
  };

  return (
    <div style={{ background: "#262b40" }}>
      <div
        className="d-flex justify-content-center px-2 align-items-center"
        style={{ height: "100vh", width: "100vw", backgroundSize: "cover" }}
      >
        <div style={{ width: "400px", maxWidth: "100%", color: "orange" }}>
          <h2 style={{ color: "darkorange" }}>Reset Password</h2>
          <Form onSubmit={handleSubmit} className="mt-3">
            <Row>
              <Col xs={12}>
                <Form.Group>
                  <Form.Label>New Password</Form.Label>
                  <Form.Control
                    type="password"
                    required
                    value={newPassword}
                    onChange={(e) => setNewPassword(e.target.value)}
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row className="mt-3">
              <Col xs={12}>
                <Form.Group>
                  <Form.Label>Confirm New Password</Form.Label>
                  <Form.Control
                    type="password"
                    required
                    value={confirmedNewPassword}
                    onChange={(e) => setConfirmedNewPassword(e.target.value)}
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row className="mt-5">
              <Col xs={12}>
                <Button
                  style={{
                    width: "100%",
                    background: "linear-gradient(to right, #fb7b4a, #ee3344)",
                  }}
                  type="submit"
                >
                  Reset Password
                </Button>
              </Col>
            </Row>
          </Form>
        </div>
      </div>
      {error.status && <GlobalToast variant="danger" message={error.message} />}
    </div>
  );
};

export default ResetPassword;
